/*UNIVERSAL DEFAULT*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*BODY LAYOUT*/

body {
  background-color: #101116;
  color: #ededed;
  font-family: "Ubuntu Mono", monospace;
  padding: 49px 69px 0px;
}

/*HEADER LAYOUT*/

.logo {
  padding: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.logo img {
  width: 69px;
  height: 69px;
}

.header {
  margin-bottom: 40px;

  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.hidden {
  display: none !important;
}

.message {
  text-align: center;
  text-transform: uppercase;
  font-size: 28px;
  font-weight: 700;
  margin-top: 22px;
}

.disputed {
  color: orangered;
  font-weight: 700;
  margin-right: 10px;
}

/*PRIMARY LAYOUT*/

#root {
  max-width: 1580px;
  margin: 0 auto;
}

.main {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 45px;
  height: calc(100vh - 89px - 40px - 49px - 49px);
}

@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
  }
}

section {
  overflow: scroll;
  padding-bottom: 40px;
}

.fact {
  font-size: 18px;
  line-height: 1.4;
  background-color: #373244;
  margin-bottom: 18px;
  padding: 16px 24px;
  letter-spacing: 0.5px;

  display: flex;
  align-items: center;
  gap: 22px;
}

@media (max-width: 1300px) {
  .fact {
    flex-direction: column;
    align-items: flex-end;
    gap: 18px;
  }
}

.tag {
  text-transform: uppercase;
  font-size: 14px;
  font-family: "Tektur", cursive;
  padding-left: 10px;
  padding-right: 10px;
  line-height: 1.2;
  cursor: pointer;
}

/*ELEMENTS LAYOUT*/

h1 {
  font-size: 42px;
  text-transform: uppercase;
  font-family: "Tektur", cursive;
  color: #ffe79b;
}

ul {
  list-style: none;
}

/*BUTTONS LAYOUT*/

.source:link,
.source:visited {
  color: #dd58d6;
  text-decoration: none;
  margin-left: 11px;
  transition: 0.4s;
}

.source:hover {
  color: #ffe79b;
}

.source:active {
  color: #dd58d6;
}

.btn {
  border: none;
  font-family: "Tektur", cursive;
  line-height: 1;
  text-transform: uppercase;
  font-size: 18px;
  padding: 16px 0 14px;
  background-image: linear-gradient(to right, #dd58d6, #40128b);
  color: inherit;
  cursor: pointer;
  transition: 0.4s;
}

.btn:hover {
  transform: scale(116%) rotate(2deg);
}

.btn-large {
  font-size: 18px;
  padding: 12px 25px 12px;
}

.btn-categories {
  margin-bottom: 30px;
  width: 100%;
}

.btn-category {
  width: 100%;
}

.category {
  margin-bottom: 18px;
}

.vote-buttons {
  margin-left: auto;

  display: flex;
  flex-shrink: 0;
  gap: 7px;
}

.vote-buttons button {
  border: none;
  background-color: #665e7e;
  font-size: 14px;
  padding: 4px 8px;
  color: inherit;
  font-family: inherit;
  cursor: pointer;
  transition: 0.4s;
}

.vote-buttons button:hover {
  background-color: #40128b;
}

.vote-buttons button:disabled {
  background-color: #373244;
}

/*FORM LAYOUT*/

.fact-form {
  background-color: #373244;
  margin-bottom: 40px;
  padding: 16px 24px;

  display: flex;
  align-items: center;
  gap: 16px;
}

.fact-form input,
.fact-form select {
  width: 220px;
  border: none;
  background-color: #665e7e;
  padding: 10px;
  color: inherit;
  font-family: inherit;
}

.fact-form input::placeholder {
  color: #867ca5;
}

.fact-form input:first-child {
  flex-grow: 1;
}

.fact-form span {
  font-size: 18px;
  margin-right: 18px;
}

@media (max-width: 900px) {
  .body {
    padding: 32px 48px;
  }

  .fact-form {
    flex-direction: column;
    align-items: stretch;
  }

  .fact-form input,
  .fact-form select {
    width: auto;
  }
}

/*FOOTER LAYOUT*/

.footer {
  margin-top: 169px;
}

.footer-container {
  max-width: 1580px;
  margin: 0 auto;

  display: flex;
  justify-content: start;
  align-items: center;
}
